import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";
import ModifyOperations from "./common/modifyOperations";

const FactDetBehiviorMng = () => {
    const { node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const mng = (node.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeParamManager;


    /**  */
    const isVector = useMemo(() => {
        const swapPos = node.children.length - 2;
        return ((node.children[swapPos].data as FactoryUtil.WrapNode).data as FactoryUtil.NodeParamItem).name === 'vec_x';
    }, [node]);

    return (<>
        <FormUtil.BorderPanel
            title="element"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Vector method'}
                            width={300}
                            isEnable={!isVector}
                            isUse={isVector}
                            callback={() => {
                                const swapPos = node.children.length - 2;
                                node.children.splice(swapPos, 2);
                                {
                                    const wrap = FactoryUtil.createParamWrap('behavior-item',
                                        { name: 'vec_x', formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] });
                                    mng.params.push(wrap);
                                    node.children.push(TreeUtil.createNode(wrap, node));
                                }
                                {
                                    const wrap = FactoryUtil.createParamWrap('behavior-item',
                                        { name: 'vec_y', formulaItems: ['var'], values: [{ type: 'direct', value: '-10' }] });
                                    mng.params.push(wrap);
                                    node.children.push(TreeUtil.createNode(wrap, node));
                                }
                                invalidate();
                                setFocusNode({ ...node });
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Direction method'}
                            width={300}
                            isEnable={isVector}
                            isUse={!isVector}
                            callback={() => {
                                const swapPos = node.children.length - 2;
                                node.children.splice(swapPos, 2);
                                {
                                    const wrap = FactoryUtil.createParamWrap('behavior-item',
                                        { name: 'radian', formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] });
                                    mng.params.push(wrap);
                                    node.children.push(TreeUtil.createNode(wrap, node));
                                }
                                {
                                    const wrap = FactoryUtil.createParamWrap('behavior-item',
                                        { name: 'length', formulaItems: ['var'], values: [{ type: 'direct', value: '10' }] });
                                    mng.params.push(wrap);
                                    node.children.push(TreeUtil.createNode(wrap, node));
                                }
                                invalidate();
                                setFocusNode({ ...node });
                            }}
                        />
                    }
                />
            </>}
        />
    </>);
}

export default FactDetBehiviorMng;