import { type } from "os";
import { useContext, useEffect, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";
import ModifyOperations from "./common/modifyOperations";

const FactDetArgumentItem = () => {
    const { node, setFocusNode, setModify, isModify ,invalidate} = useContext(FactoryTreeContext);
    const wrap = node.data as FactoryUtil.WrapNode;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [argType, setArgType] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const mappingForm = () => {
        const param = wrap.data as FactoryUtil.NodeArgumentItem;
        setName({ ...name, value: param.name });
        setArgType({ ...argType, value: param.valueType });
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name]}
            firstRegist={() => {
                const param: FactoryUtil.NodeArgumentItem = {
                    name: name.value,
                    valueType: argType.value as FactoryUtil.ArgumentType
                };
                param.name = name.value;
                wrap.data = param;
            }}
            modifyRegist={() => {
                const param = wrap.data as FactoryUtil.NodeArgumentItem;
                param.name = name.value;
                param.valueType = argType.value as FactoryUtil.ArgumentType;
                wrap.data = param;
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => FactoryUtil.checkFieldName(value),
                                    errorType: "value"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Type"
                    jsx={
                        <FormUtil.Combobox
                            checkable={argType}
                            setCheckable={setArgType}
                            list={FactoryUtil.ArgTypes.map(type => {
                                return { value: type, labelText: type, isDisabled: type !== 'float'}
                            })}
                            headBlank
                            width={200}
                            isReadOnly={!isModify}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    }
                                ]
                            }
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                let index = -1;
                                node.parent?.children.forEach((n, i) => {
                                    if (n == node) index = i;
                                });
                                node.parent?.children.splice(index, 1);
                                ((node.parent?.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeArgumentManager).args.splice(index, 1);
                                setFocusNode(node.parent as TreeUtil.ElementNode);
                                invalidate();
                            }
                        }
                    ]}
                />
            </>}
        />
    </>);
}

export default FactDetArgumentItem;