import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";
import ModifyOperations from "./common/modifyOperations";

const FactDetInheritConnector = () => {
    const { rootData, node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as FactoryUtil.WrapNode;

    const [condition, setCondition] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [connectType, setConnectType] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [depend, setDepend] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const [hasIterateNode] = useMemo(() => {
        const list = node.children.map(el => {
            return (el.data as FactoryUtil.WrapNode).type;
        });
        return [
            list.includes('iterate-mng'),
        ]
    }, [node, node.children.length]);

    const mappingForm = () => {
        const param = wrap.data as FactoryUtil.NodeInheritConnector;
        setCondition({ ...condition, value: param.condition });
        setConnectType({ ...connectType, value: param.connectType });
        setDepend({ ...depend, value: param.depend });
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    const isCreate = connectType.value === 'create';

    const inheritList = useMemo((): string[] => {
        const rootNode = rootData.data as FactoryUtil.NodeRoot;
        const inheritMng = rootNode.inheritMng.data as FactoryUtil.NodeLogicManager;
        return inheritMng.defs.map(wrap => {
            return (wrap.data as FactoryUtil.NodeLogicItem).name;
        });
    }, [node]);

    const getSendParamList = (): string[] => {
        if (!isCreate) {
            const rootNode = rootData.data as FactoryUtil.NodeRoot;
            const inheritMng = rootNode.inheritMng.data as FactoryUtil.NodeLogicManager;
            const dependItem = inheritMng.defs.find(wrap => {
                return (wrap.data as FactoryUtil.NodeLogicItem).name === depend.value;
            });
            if (dependItem != undefined) {
                const argWrap = (dependItem.data as FactoryUtil.NodeLogicItem).mngs[0];
                if (argWrap.type === 'argument-mng') {
                    return (argWrap.data as FactoryUtil.NodeArgumentManager).args.map(arg => {
                        return (arg.data as FactoryUtil.NodeArgumentItem).name;
                    });
                }
            }
        }
        return [];
    }

    const setSendParams = (param: FactoryUtil.NodeInheritConnector) => {

        const sendIndex = param.mngs[0].type === 'send-mng' ? 0 : 1;
        const sendMng = param.mngs[sendIndex].data as FactoryUtil.NodeParamManager;
        const sendNode = node.children[sendIndex];
        sendMng.params = [];
        sendNode.children = [];
        getSendParamList().forEach(param => {
            const paramWrap = FactoryUtil.createParamWrap('origin-item',
                { name: param, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
            );
            sendMng.params.push(paramWrap);
            sendNode.children.push(TreeUtil.createNode(paramWrap, sendNode));
        });
    }

    const initConnectorNode = (param: FactoryUtil.NodeInheritConnector) => {
        param.mngs = [];
        node.children = [];
        param.mngs.push({
            type: 'send-mng',
            data: {
                params: []
            } as FactoryUtil.NodeParamManager
        });
        if (isCreate) {
            param.mngs.push({
                type: 'inherit-cache-item',
                data: {
                    mngs: FactoryUtil.getDefaultLogicMngs(true)
                } as FactoryUtil.NodeConnectInheritItem
            });
        }
        wrap.data = param;

        param.mngs.forEach(mng => {
            const newNode = TreeUtil.createNode(mng, node);
            const data = (newNode.data as FactoryUtil.WrapNode);
            switch (data.type) {
                case 'inherit-cache-item': {
                    (data.data as FactoryUtil.NodeConnectInheritItem).mngs.forEach(mng => {
                        const inheritNode = TreeUtil.createNode(mng, newNode);
                        newNode.children.push(inheritNode);

                        switch (mng.type) {
                            case 'visual-mng': {
                                (mng.data as FactoryUtil.NodeParamManager).params.forEach(param => {
                                    inheritNode.children.push(TreeUtil.createNode(param, inheritNode));
                                });
                            } break;
                            case 'behavior-mng': {
                                (mng.data as FactoryUtil.NodeParamManager).params.forEach(param => {
                                    inheritNode.children.push(TreeUtil.createNode(param, inheritNode));
                                });
                            } break;
                        }
                    });
                } break;
            }
            node.children.push(newNode);
        });
        setSendParams(param);
    }

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[condition, connectType, depend]}
            firstRegist={() => {
                const param: FactoryUtil.NodeInheritConnector = {
                    condition: condition.value as FactoryUtil.ConditionType,
                    connectType: connectType.value as FactoryUtil.ConnectType,
                    depend: depend.value,
                    mngs: []
                };
                initConnectorNode(param);
                invalidate();
            }}
            modifyRegist={() => {
                const param = wrap.data as FactoryUtil.NodeInheritConnector;
                const isChange = param.connectType !== connectType.value || param.depend !== depend.value;
                param.condition = condition.value as FactoryUtil.ConditionType;
                param.connectType = connectType.value as FactoryUtil.ConnectType;
                param.depend = depend.value;
                wrap.data = param;

                if (isChange) {
                    initConnectorNode(param);
                }
                invalidate();
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Condition"
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={condition}
                            width={178}
                            isReadOnly={!isModify}
                            setCheckable={setCondition}
                            list={FactoryUtil.ConditionTypes.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Inherit"
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={connectType}
                            width={178}
                            isReadOnly={!isModify}
                            setCheckable={(checkable) => {
                                // createdepend
                                if (checkable.value === 'create') {
                                    setDepend({ ...depend, value: '' });
                                }
                                setConnectType(checkable);
                            }}
                            list={FactoryUtil.ConnectTypes.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Depend"
                    isEnable={connectType.value === 'depend'}
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={depend}
                            width={178}
                            isReadOnly={!isModify}
                            setCheckable={setDepend}
                            list={inheritList.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            dependency={[connectType]}
                            validates={[
                                {
                                    checker: (value) => {
                                        return connectType.value !== 'depend' || ValidateUtil.checkRequired(value);
                                    },
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="element"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={<FormUtil.Switch
                        label={'Iterate'}
                        width={300}
                        isUse={hasIterateNode}
                        callback={() => {
                            const data = (wrap.data as FactoryUtil.NodeLogicItem);
                            if (hasIterateNode) {
                                data.mngs.splice(0, 1);
                                node.children.splice(0, 1);
                                setFocusNode({ ...node });
                                invalidate();
                            } else {
                                const newWrap: FactoryUtil.WrapNode = {
                                    type: 'iterate-mng',
                                    data: {
                                        params: []
                                    } as FactoryUtil.NodeParamManager
                                };

                                data.mngs.unshift(newWrap);
                                const newNode = TreeUtil.createNode(newWrap, node);
                                node.children.unshift(newNode);

                                const loopCountWrap = FactoryUtil.createParamWrap('iterate-item',
                                    { name: 'loop_count', formulaItems: ['var'], values: [{ type: 'direct', value: '1' }] });
                                (newWrap.data as FactoryUtil.NodeParamManager).params.push(loopCountWrap);
                                newNode.children.push(TreeUtil.createNode(loopCountWrap, newNode));

                                setFocusNode({ ...node });
                                invalidate();
                            }
                        }}
                    />}
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<>
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Delete',
                            callback: () => {
                                let index = -1;
                                node.parent?.children.forEach((n, i) => {
                                    if (n == node) index = i;
                                });
                                node.parent?.children.splice(index, 1);
                                ((node.parent?.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeInheritConnectManager).connects.splice(index, 1);
                                setFocusNode(node.parent as TreeUtil.ElementNode);
                                invalidate();
                            }
                        }
                    ]}
                />
            </>}
        />
    </>);
}

export default FactDetInheritConnector;