import { useContext } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import TreeUtil from "../../../../utils/treeUtil";
import FactoryTab, { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";

const FactDetSendManager = () => {
    const { rootData, node, setFocusNode, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as FactoryUtil.WrapNode;

    const getSendParamList = (): string[] => {
        const connector = (node.parent?.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeInheritConnector;
        const isCreate = connector.connectType === 'create';
        let targetWrap: FactoryUtil.WrapNode | null = null;
        if (!isCreate) {
            const rootNode = rootData.data as FactoryUtil.NodeRoot;
            const inheritMng = rootNode.inheritMng.data as FactoryUtil.NodeLogicManager;
            const dependItem = inheritMng.defs.find(wrap => {
                return (wrap.data as FactoryUtil.NodeLogicItem).name === connector.depend;
            });
            if (dependItem != undefined) targetWrap = dependItem;
        } else {
            targetWrap = (node.parent?.children[node.parent?.children.length - 1].data as FactoryUtil.WrapNode)
        }
        if (targetWrap != null) {
            const argWrap = (targetWrap.data as FactoryUtil.NodeLogicItem).mngs[0];
            if (argWrap.type === 'argument-mng') {
                return (argWrap.data as FactoryUtil.NodeArgumentManager).args.map(arg => {
                    return (arg.data as FactoryUtil.NodeArgumentItem).name;
                });
            }
        }
        return [];
    }

    const update = () => {
        const sendParamList = getSendParamList();
        const curSize = node.children.length;
        const params = (wrap.data as FactoryUtil.NodeParamManager).params;
        // 
        for (let i = curSize - 1; i >= 0; i--) {
            const name = (params[i].data as FactoryUtil.NodeParamItem).name;
            if (!sendParamList.includes(name)) {
                node.children.splice(i, 1);
                params.splice(i, 1);
            }
        }
        // 
        const curParamList = params.map(param => {
            return (param.data as FactoryUtil.NodeParamItem).name;
        });
        sendParamList.forEach(param => {
            if (!curParamList.includes(param)) {
                const paramWrap = FactoryUtil.createParamWrap('send-item',
                    { name: param, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
                );
                params.push(paramWrap);
                node.children.push(TreeUtil.createNode(paramWrap, node));
            }
        });
    }

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                isCenter
                buttons={[
                    {
                        label: 'Update Params',
                        callback: () => {
                            update();
                            invalidate();
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default FactDetSendManager;