import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import ConfirmDialog from "../../../dialog/confirmDialog";
import { GlobalContext } from "../../../entry/systemEntry";
import { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";
import LogicPreviewFrame from "../../preview/logicPreviewFrame";
import ModifyOperations from "./common/modifyOperations";

const FactDetLogicItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { rootData, node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as FactoryUtil.WrapNode;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const [isConnect, isInherit, hasArgNode, hasFieldNode, hasExtendNode] = useMemo(() => {
        const list = node.children.map(el => {
            return (el.data as FactoryUtil.WrapNode).type;
        });
        const parentType = (node.parent?.data as FactoryUtil.WrapNode).type;
        return [
            parentType === 'connector',
            parentType !== 'origin-mng',
            list.includes('argument-mng'),
            list.includes('field-mng'),
            list.includes('extend-mng'),
        ]
    }, [node]);

    const mappingForm = () => {
        if (!isConnect) {
            const param = wrap.data as FactoryUtil.NodeLogicItem;
            setName({ ...name, value: param.name });
        }
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node, isConnect]);

    return (<>
        {isConnect ? <></> : (<>
            <ModifyOperations.Component
                mappingForm={mappingForm}
                checkableFields={[name]}
                firstRegist={() => {
                    const param: FactoryUtil.NodeLogicItem = {
                        name: name.value,
                        mngs: FactoryUtil.getDefaultLogicMngs(isInherit)
                    };
                    param.name = name.value;
                    wrap.data = param;

                    param.mngs.forEach(mng => {
                        const newNode = TreeUtil.createNode(mng, node);
                        const data = (newNode.data as FactoryUtil.WrapNode);
                        switch (data.type) {
                            case 'visual-mng': {
                                (data.data as FactoryUtil.NodeParamManager).params.forEach(param => {
                                    newNode.children.push(TreeUtil.createNode(param, newNode));
                                });
                            } break;
                            case 'behavior-mng': {
                                (data.data as FactoryUtil.NodeParamManager).params.forEach(param => {
                                    newNode.children.push(TreeUtil.createNode(param, newNode));
                                });
                            } break;
                        }
                        node.children.push(newNode);
                    });
                }}
                modifyRegist={() => {
                    const param = wrap.data as FactoryUtil.NodeLogicItem;
                    param.name = name.value;
                    wrap.data = param;
                }}
            />
            <FormUtil.BorderPanel
                title="param"
                innerJsx={<>
                    <FormUtil.FormRecord
                        titleLabel="Name"
                        jsx={
                            <FormUtil.TextField
                                width={200}
                                isReadOnly={!isModify}
                                checkable={name}
                                setCheckable={setName}
                                validates={[
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    },
                                    {
                                        checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                        errorType: "length"
                                    },
                                    {
                                        checker: (value) => FactoryUtil.checkFieldName(value),
                                        errorType: "value"
                                    }
                                ]}
                            />
                        }
                    />
                </>}
            />
        </>)}
        <FormUtil.BorderPanel
            title="element"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={<FormUtil.Switch
                        label={'Argument'}
                        width={300}
                        isUse={hasArgNode}
                        callback={() => {
                            const data = (wrap.data as FactoryUtil.NodeLogicItem);
                            if (hasArgNode) {
                                data.mngs.splice(0, 1);
                                node.children.splice(0, 1);
                                setFocusNode({ ...node });
                                invalidate();
                            } else {
                                const newWrap: FactoryUtil.WrapNode = {
                                    type: 'argument-mng',
                                    data: {
                                        args: []
                                    } as FactoryUtil.NodeArgumentManager
                                }
                                data.mngs.unshift(newWrap);
                                const newNode = TreeUtil.createNode(newWrap, node);
                                node.children.unshift(newNode);
                                setFocusNode({ ...node });
                                invalidate();
                            }
                        }}
                    />}
                />
                <FormUtil.Record
                    textAlign="center"
                    isEnable={false}
                    jsx={<FormUtil.Switch
                        label={'Field'}
                        width={300}
                        isUse={hasFieldNode}
                        callback={() => {
                            if (hasFieldNode) {
                            } else {
                                const newWrap: FactoryUtil.WrapNode = {
                                    type: 'field-mng',
                                    data: {
                                        args: []
                                    } as FactoryUtil.NodeFieldManager
                                }
                                const data = (wrap.data as FactoryUtil.NodeLogicItem);
                                data.mngs.push(newWrap);
                                const newNode = TreeUtil.createNode(newWrap, node);
                                node.children.push(newNode);
                                setFocusNode(newNode);
                            }
                            setModify(true);
                        }}
                    />}
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Extend'}
                            width={300}
                            isUse={hasExtendNode}
                            callback={() => {
                                const data = (wrap.data as FactoryUtil.NodeLogicItem);
                                if (hasExtendNode) {
                                    console.log(data.mngs[data.mngs.length - 1].type);
                                    data.mngs.splice(data.mngs.length - 1, 1);
                                    node.children.splice(node.children.length - 1, 1);
                                    setFocusNode({ ...node });
                                    invalidate();
                                } else {
                                    const newWrap: FactoryUtil.WrapNode = {
                                        type: 'extend-mng',
                                        data: {
                                            mngs: []
                                        } as FactoryUtil.NodeExtendManager
                                    }
                                    data.mngs.push(newWrap);
                                    const newNode = TreeUtil.createNode(newWrap, node);
                                    node.children.push(newNode);
                                    setFocusNode({ ...node });
                                    invalidate();
                                }
                            }}
                        />}
                />
            </>}
        />
        {isConnect ? <></> : (
            <FormUtil.BorderPanel
                title="operation"
                isEnable={!isModify}
                innerJsx={<>
                    {/* {isInherit ? <></> : (
                        <FormUtil.ButtonRecord
                            isCenter
                            buttons={[
                                {
                                    label: 'Preview',
                                    callback: () => {
                                        store.system.dialog = (
                                            <LogicPreviewFrame.Component
                                                node={node}
                                                rootData={rootData}
                                            />
                                        );
                                        setStore({ ...store });
                                    }
                                }
                            ]}
                        />
                    )} */}
                    <FormUtil.ButtonRecord
                        isCenter
                        buttons={[
                            {
                                label: 'Delete',
                                callback: () => {
                                    let index = -1;
                                    node.parent?.children.forEach((n, i) => {
                                        if (n == node) index = i;
                                    });
                                    node.parent?.children.splice(index, 1);
                                    ((node.parent?.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeLogicManager).defs.splice(index, 1);
                                    setFocusNode(node.parent as TreeUtil.ElementNode);
                                    invalidate();
                                }
                            }
                        ]}
                    />
                </>}
            />
        )}
    </>);
}

export default FactDetLogicItem;