    export const getInitialData = (): WrapNode => {
        return {
            type: 'root',
            data: {
                seedMng: {
                    type: 'origin-mng',
                    data: {
                        defs: []
                    } as NodeLogicManager
                } as WrapNode,
                inheritMng: {
                    type: 'inherit-mng',
                    data: {
                        defs: []
                    } as NodeLogicManager
                } as WrapNode
            } as NodeRoot
        }
    }

    const COLOR_ROOT = '#a1a1a1';
    const COLOR_METHOD = '#cca67b';
    const COLOR_MNG = '#37a2bd';
    const COLOR_LIST = '#b85f5f';
    const COLOR_ITEM = '#ae628c';
    const COLOR_PARAM = '#8dae62';

    export const getNodeJsx = (node: TreeUtil.ElementNode, isFocus: boolean) => {
        const wrap = node.data as WrapNode;
        switch (wrap.type) {
            case 'root': {
                return <_Category backgroundColor={COLOR_ROOT}>Logic</_Category>;
            }
            case 'origin-mng': {
                return <_Category backgroundColor={COLOR_METHOD}>Origin Manager</_Category>;
            }
            case 'inherit-mng': {
                return <_Category backgroundColor={COLOR_METHOD}>Inherit Manager</_Category>;
            }
            case 'visual-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Visual</_Category>;
            }
            case 'extend-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Extend</_Category>;
            }
            case 'modulate-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Modulate</_Category>;
            }
            case 'origin-item': {
                let name = '...';
                if (wrap.data != null) {
                    const data = wrap.data as NodeLogicItem;
                    name = data.name;
                }
                return <>
                    <_Category backgroundColor={COLOR_ITEM}>Origin</_Category>
                    <_EditableValue>{name}</_EditableValue>
                </>;
            }
            case 'inherit-item': {
                let name = '...';
                if (wrap.data != null) {
                    const data = wrap.data as NodeLogicItem;
                    name = data.name;
                }
                return <>
                    <_Category backgroundColor={COLOR_ITEM}>Inherit</_Category>
                    <_EditableValue>{name}</_EditableValue>
                </>;
            }
            case 'inherit-cache-item': {
                return <_Category backgroundColor={COLOR_MNG}>Inherit</_Category>;
            }
            case 'argument-mng': {
                return <_Category backgroundColor={COLOR_LIST}>Argument Manager</_Category>;
            }
            case 'behavior-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Behavior</_Category>;
            }
            case 'connect-mng': {
                return <_Category backgroundColor={COLOR_LIST}>Connect Manager</_Category>;
            }
            case 'connector': {
                let detailJsx = <span>...</span>;
                if (wrap.data != null) {
                    const data = wrap.data as NodeInheritConnector;
                    detailJsx = <span>{`${data.condition} ${data.connectType}`}</span>;
                    if (data.connectType === 'depend') {
                        detailJsx = <span>{`${data.condition} ${data.connectType}`}: <_Field>{data.depend}</_Field></span>;
                    }
                }
                return (<>
                    <_Category backgroundColor={COLOR_ITEM}>Connector</_Category>
                    {detailJsx == undefined ? <></> : <_EditableValue>{detailJsx}</_EditableValue>}
                </>);
            }
            case 'effect-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Effect</_Category>;
            }
            case 'modulate-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Modulate</_Category>;
            }
            case 'visual-item':
            case 'send-item':
            case 'iterate-item':
            case 'effect-item':
            case 'modulate-item':
            case 'behavior-item': {
                const data = wrap.data as NodeParamItem;
                let result: JSX.Element[] = [];
                let varCnt = 0;
                data.formulaItems.forEach((formula, i) => {
                    let str = <_Signature key={i}>{formula}</_Signature>;
                    if (formula === 'var') {
                        const v = data.values[varCnt];
                        if (v.type !== 'direct') str = <_Field key={i}>{`$\{${v.value}}`}</_Field>;
                        else str = <span key={i}>{v.value}</span>;
                        varCnt++;
                    }
                    result.push(str);
                });
                return <>
                    <_Category backgroundColor={COLOR_PARAM}>Param<_Detail>&nbsp;[{data.name}]</_Detail></_Category>
                    <_EditableValue>{result}</_EditableValue>
                </>;
            }
            case 'argument-item': {
                let detailJsx = <span>...</span>;
                if (wrap.data != null) {
                    const data = wrap.data as NodeArgumentItem;
                    detailJsx = <span>{data.name}<_ValueType>&nbsp;({data.valueType})</_ValueType></span>
                }
                return <>
                    <_Category backgroundColor={COLOR_ITEM}>Argument<_Detail></_Detail></_Category>
                    <_EditableValue>{detailJsx}</_EditableValue>
                </>;
            }
            case 'iterate-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Iterate</_Category>;
            }
            case 'send-mng': {
                return <_Category backgroundColor={COLOR_MNG}>Send</_Category>;
            }
        }
        return <_Category backgroundColor={'#ffffff'}>XXXXX</_Category>;
    }

    export const getTypeName = (node: TreeUtil.ElementNode | null) => {
        if (node == null) return '';
        const type = (node.data as WrapNode).type;
        switch (type) {
            case 'root': return 'Root';
            case 'origin-mng': return 'Origin Manager';
            case 'inherit-mng': return 'Inherit Manager';
            case 'origin-item': return 'Origin Item';
            case 'inherit-item': return 'Inherit Item';
            case 'argument-mng': return 'Argument Manager';
            case 'visual-mng': return 'Visual Manager';
            case 'behavior-item': return 'Behavior Item';
            case 'visual-item': return 'Visual Item';
            case 'argument-item': return 'Argument Item';
            case 'extend-mng': return 'Extend Manager';
            case 'behavior-mng': return 'Behivior Manager';
            case 'connect-mng': return 'Inherit Connect Manager';
            case 'connector': return 'Inherit Connector';
            case 'inherit-cache-item': return 'Inherit Cache Item';
            case 'effect-mng': return 'Effect Manager';
            case 'modulate-mng': return 'Modulate Manager';
            case 'iterate-mng': return 'Iterate Manager';
            case 'send-mng': return 'Send Manager';
            case 'iterate-item': return 'Iterate Item';
            case 'send-item': return 'Send Item';
            case 'modulate-item': return 'Modulate Item';
            case 'effect-item': return 'Effect Item';
        }
    }

    export const getDetailComponent = (node: TreeUtil.ElementNode | null) => {
        if (node == null) return <></>;
        const type = (node.data as WrapNode).type;
        switch (type) {
            case 'root': return <></>;
            case 'origin-mng': return <FactDetOriginMng />;
            case 'inherit-mng': return <FactDetInheritMng />;
            case 'origin-item': return <FactDetLogicItem />;
            case 'inherit-item': return <FactDetLogicItem />;
            case 'argument-mng': return <FactDetArgumentMng />;
            case 'argument-item': return <FactDetArgumentItem />;
            case 'visual-mng': return <FactDetVisualMng />;
            case 'extend-mng': return <FactDetExtendMng />;
            case 'behavior-mng': return <FactDetBehiviorMng />;
            case 'connect-mng': return <FactDetInheritConnMng />;
            case 'effect-mng': return <FactDetEffectMng />;
            case 'modulate-mng': return <FactDetModulateMng />;
            case 'connector': return <FactDetInheritConnector />;
            case 'inherit-cache-item': return <FactDetLogicItem />;
            case 'send-mng': return <FactDetSendManager />;
            case 'iterate-item':
            case 'visual-item':
            case 'behavior-item':
            case 'effect-item':
            case 'modulate-item':
            case 'send-item': return <FactOprParam />;
        }
    }

