import { type } from "os";
import { useCallback, useEffect, useMemo, useState } from "react";
import styled, { css } from "styled-components";
import Styles from "../../../base/design/styles";
import TreeUtil from "../../../utils/treeUtil";
import Common from "../common";

namespace TimelineUtil {


    export type NodeType =
        'root' |
        'bv'
        ;

    export type WrapNode = {
        type: NodeType;
        data: any;
    }

    export type NodeRoot = {
        // caches: Common.Cache[];
    }

    export const getInitialData = (): WrapNode => {
        return {
            type: 'root',
            data: {}
        }
    }

    export const getNodeJsx = (node: TreeUtil.ElementNode, isFocus: boolean) => {
        const wrap = node.data as WrapNode;
        switch(wrap.type) {
            case 'root': {
                return <_Wrap>Timeline</_Wrap>;
            }
        }
        return <_Wrap>XXXXX</_Wrap>;
    }
}

export default TimelineUtil;

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    background-color: #37a2bd;
    width: 100%;
    height: 100%;
    ${Styles.CSS_LABEL_MIDIUM}
`;