import { useContext } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import TreeUtil from "../../../../utils/treeUtil";
import FactoryTab, { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";

const FactDetInheritConnMng = () => {
    const { node, setFocusNode, invalidate } = useContext(FactoryTreeContext);
    const mng = (node.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeInheritConnectManager;

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                isCenter
                buttons={[
                    {
                        label: 'Add Connector',
                        callback: () => {
                            const wrap: FactoryUtil.WrapNode = {
                                type: 'connector',
                                data: null
                            }
                            mng.connects.push(wrap);
                            const newNode = TreeUtil.createNode(wrap, node);
                            node.children.push(newNode);
                            setFocusNode(newNode);
                            invalidate();
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default FactDetInheritConnMng;