import { assert } from "console";
import TreeUtil from "../../../utils/treeUtil";
import DrawUtil from "../../graphic/drawUtil";
import Particle from "../../graphic/particle";
import VectorUtil from "../../graphic/vectorUtil";
import FactoryUtil from "../factory/factoryUtil";

namespace PreviewUtil {

    export const SCREEN_WIDTH = 550;
    export const SCREEN_HEIGHT = 550;

    export type ValueMap = {
        name: string;
        value: number;
    }

    type ParentProps = {
        pos: VectorUtil.XY;
    }

    export const buildOriginProps = (
        owner: Particle.Instance[],
        node: TreeUtil.ElementNode,
        rootData: FactoryUtil.WrapNode,
        args: ValueMap[]
    ): Particle.Props => {

        const searchDependLogic = (name: string): FactoryUtil.NodeLogicItem | null => {
            const mng = (rootData.data as FactoryUtil.NodeRoot).inheritMng.data as FactoryUtil.NodeLogicManager;
            const wrap = mng.defs.find(def => (def.data as FactoryUtil.NodeLogicItem).name === name);
            if (wrap == undefined) return null;
            return wrap.data as FactoryUtil.NodeLogicItem;
        }

        const createParticleRec = (cur: FactoryUtil.NodeLogicItem, fields: ValueMap[], parent: null | ParentProps): Particle.Props => {
            let valueMapList: ValueMap[] = fields.concat(ConstantValueMap);

            const visualParams = (searchElement('visual-mng', cur.mngs)?.data as FactoryUtil.NodeParamManager).
                params.map(param => param.data as FactoryUtil.NodeParamItem);
            const behaviorParams = (searchElement('behavior-mng', cur.mngs)?.data as FactoryUtil.NodeParamManager).
                params.map(param => param.data as FactoryUtil.NodeParamItem);

            const getVisualParam = (name: string) => {
                return convertFormulaToValue(name, visualParams, valueMapList);
            }
            const getBehaviorParam = (name: string) => {
                return convertFormulaToValue(name, behaviorParams, valueMapList);
            }

            const limit = Math.floor(getBehaviorParam('limit'));
            const pos = parent == null ? { x: getBehaviorParam('pos_x'), y: getBehaviorParam('pos_y') } : parent.pos;
            const vec = new VectorUtil.Vector({ x: 0, y: 0 });
            if (behaviorParams[behaviorParams.length - 1].name === 'vec_y') {
                vec.xy.x = getBehaviorParam('vec_x');
                vec.xy.y = getBehaviorParam('vec_y');
                vec.syncFromXY();
            } else {
                vec.rad = getBehaviorParam('radian');
                vec.len = getBehaviorParam('length');
                vec.syncFromRad();
            }

            const extendList: ((self: Particle.Instance) => void)[] = [];
            const extendMng = searchElement('extend-mng', cur.mngs);

            let disappearStartRate = undefined;
            if (extendMng != null) {
                const mngs = (extendMng.data as FactoryUtil.NodeExtendManager).mngs;
                const connectMng = searchElement('connect-mng', mngs);
                if (connectMng != null) {
                    const connects = (connectMng.data as FactoryUtil.NodeInheritConnectManager).connects;

                    connects.forEach(connect => {
                        const connector = connect.data as FactoryUtil.NodeInheritConnector;

                        // 
                        const getCondition: ((self: Particle.Instance) => boolean) = (connector.condition === 'limit') ? (self: Particle.Instance) => {
                            return self.frame === self.limit;
                        } : (self: Particle.Instance) => {
                            return true;
                        };
                        const logicItem = connector.connectType === 'create' ?
                            searchElement('inherit-cache-item', connector.mngs)?.data as FactoryUtil.NodeLogicItem :
                            searchDependLogic(connector.depend);

                        if (logicItem != null) {
                            let loopCnt = 1;
                            const iterateItem = searchElement('iterate-mng', connector.mngs);
                            if (iterateItem != null) {
                                const params = (iterateItem.data as FactoryUtil.NodeParamManager).params.map(param => param.data as FactoryUtil.NodeParamItem);
                                loopCnt = convertFormulaToValue('loop_count', params, valueMapList);
                            }

                            valueMapList.push({ name: 'limit', value: limit });
                            valueMapList.push({ name: 'vec_x', value: vec.xy.x });
                            valueMapList.push({ name: 'vec_y', value: vec.xy.y });
                            valueMapList.push({ name: 'radian', value: vec.rad });
                            valueMapList.push({ name: 'length', value: vec.len });

                            // 
                            for (let i = 0; i < loopCnt; i++) {

                                // 
                                let iterateValueMap: ValueMap[] = [];
                                if (iterateItem != null) {
                                    iterateValueMap = [
                                        { name: 'loop_count', value: loopCnt },
                                        { name: 'loop_iterator', value: i }
                                    ]
                                }

                                const sendItem = searchElement('send-mng', connector.mngs)?.data as FactoryUtil.NodeParamManager;
                                const params = sendItem.params.map(param => param.data as FactoryUtil.NodeParamItem);
                                const sendValueMapList: ValueMap[] = params.map(item => {
                                    return { name: item.name, value: convertFormulaToValue(item.name, params, valueMapList.concat(iterateValueMap)) }
                                });
                                extendList.push((self: Particle.Instance) => {
                                    if (getCondition(self)) {
                                        const props = createParticleRec(
                                            logicItem,
                                            sendValueMapList,
                                            {
                                                pos: { ...self.pos }
                                            }
                                        );
                                        self.owner.push(new Particle.Instance(props));
                                    }
                                });
                            }
                        }
                    });
                }
                const effectMng = searchElement('effect-mng', mngs);
                if (effectMng != null) {
                    const params = (effectMng.data as FactoryUtil.NodeParamManager).params.map(param => (param.data as FactoryUtil.NodeParamItem));

                    const getEffectParam = (name: string) => {
                        return convertFormulaToValue(name, params, valueMapList);
                    }
                    disappearStartRate = getEffectParam('disappear_start_rate');
                    console.log(disappearStartRate);
                }
                const modulateMng = searchElement('modulate-mng', mngs);
                if (modulateMng != null) {
                    const params = (modulateMng.data as FactoryUtil.NodeParamManager).params.map(param => (param.data as FactoryUtil.NodeParamItem));

                    const getModulateParam = (name: string) => {
                        return convertFormulaToValue(name, params, valueMapList);
                    }
                    const decayLimit = getModulateParam('decay_limit');
                    const decayRate = getModulateParam('decay_rate');
                    const gravity = getModulateParam('gravity');
                    extendList.push((self: Particle.Instance) => {
                        modulate(self, {
                            decayLimit, decayRate, gravity
                        });
                    });
                }
            }

            return {
                owner, pos, vec,
                limit,
                drawer: DrawUtil.createGradDrawer({
                    radius: getVisualParam('radius'),
                    color: DrawUtil.convertColorCodeFromRgba({
                        r: getVisualParam('color_r'),
                        g: getVisualParam('color_g'),
                        b: getVisualParam('color_b'),
                        a: getVisualParam('color_a'),
                    }),
                    disappearStartRate
                }),
                extend: extendList
            };
        }

        const origin = (node.data as FactoryUtil.WrapNode).data as FactoryUtil.NodeLogicItem;
        return createParticleRec(origin, args, null);
    }


    const searchElement = (type: FactoryUtil.NodeType, warpList: FactoryUtil.WrapNode[]) => {
        return warpList.find(wrap => wrap.type === type) ?? null;
    }

    const convertFormulaToValue = (name: string, params: FactoryUtil.NodeParamItem[], valueMapList: ValueMap[]): number => {
        const formula = params.find(param => param.name === name);
        if (formula == undefined) return 0;

        const values = formula.values.map(v => {
            if (v.type === 'direct') return Number(v.value);
            const valueMap = valueMapList.find(m => m.name === v.value);
            if (valueMap == undefined) return 0;
            return valueMap.value;
        });

        let formulaStr = '';
        let varCnt = 0;
        formula.formulaItems.forEach((item, i) => {
            let str = item;
            if (item === 'var') {
                str = values[varCnt].toString();
                varCnt++;
            }
            else if (item === 'rand(') str = 'Math.random()*(';
            formulaStr += str;
        });
        // console.log(formulaStr);
        return Function(`return (${formulaStr})`)();
    }

    export const ConstantValueMap: ValueMap[] = [
        { name: 'screen_width', value: SCREEN_WIDTH },
        { name: 'screen_height', value: SCREEN_HEIGHT },
        { name: 'pi', value: 3.14159265359 },
        { name: 'radian_round', value: 3.14159265359 * 2 }
    ];

    type ModulateParam = {
        decayRate: number;
        decayLimit: number;
        gravity: number;
    }

    const modulate = (self: Particle.Instance, param: ModulateParam) => {

        if (self.vec.len > param.decayLimit) {
            self.vec.len = self.vec.len * param.decayRate;
            self.vec.syncFromRad();
            self.vec.xy.y += param.gravity;
            self.vec.syncFromXY();
        }
    }
}

export default PreviewUtil;