import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";
import ModifyOperations from "./common/modifyOperations";

const FactDetExtendMng = () => {
    const { node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as FactoryUtil.WrapNode;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const [hasEffectsNode, hasModulateNode, hasInheritNode] = useMemo(() => {
        const list = node.children.map(el => {
            return (el.data as FactoryUtil.WrapNode).type;
        });
        return [
            list.includes('effect-mng'),
            list.includes('modulate-mng'),
            list.includes('connect-mng'),
        ]
    }, [node]);

    const mappingForm = () => {
        const param = wrap.data as FactoryUtil.NodeLogicItem;
        setName({ ...name, value: param.name });
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <FormUtil.BorderPanel
            title="element"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Effect'}
                            width={300}
                            isUse={hasEffectsNode}
                            callback={() => {
                                const data = (wrap.data as FactoryUtil.NodeExtendManager);
                                if (hasEffectsNode) {
                                    data.mngs.splice(0, 1);
                                    node.children.splice(0, 1);
                                    setFocusNode({ ...node });
                                    invalidate();
                                } else {
                                    const newWrap: FactoryUtil.WrapNode = {
                                        type: 'effect-mng',
                                        data: {
                                            params: []
                                        } as FactoryUtil.NodeParamManager
                                    }
                                    data.mngs.unshift(newWrap);
                                    const newNode = TreeUtil.createNode(newWrap, node);
                                    node.children.unshift(newNode);

                                    {
                                        const paramWrap = FactoryUtil.createParamWrap('effect-item',
                                            { name: 'disappear_start_rate', formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] });
                                        (newWrap.data as FactoryUtil.NodeParamManager).params.push(paramWrap);
                                        newNode.children.push(TreeUtil.createNode(paramWrap, newNode));
                                    }

                                    setFocusNode({ ...node });
                                    invalidate();
                                }
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Modulate'}
                            width={300}
                            isUse={hasModulateNode}
                            callback={() => {
                                const data = (wrap.data as FactoryUtil.NodeExtendManager);
                                if (hasModulateNode) {
                                    const delIndex = !hasEffectsNode ? 0 : 1;
                                    data.mngs.splice(delIndex, 1);
                                    node.children.splice(delIndex, 1);
                                    setFocusNode({ ...node });
                                    invalidate();
                                } else {
                                    const newWrap: FactoryUtil.WrapNode = {
                                        type: 'modulate-mng',
                                        data: {
                                            params: []
                                        } as FactoryUtil.NodeParamManager
                                    }
                                    const insIndex = !hasEffectsNode ? 0 : 1;
                                    data.mngs.splice(insIndex, 0, newWrap);
                                    const newNode = TreeUtil.createNode(newWrap, node);
                                    node.children.splice(insIndex, 0, newNode);

                                    {
                                        const paramWrap = FactoryUtil.createParamWrap('modulate-item',
                                            { name: 'decay_rate', formulaItems: ['var'], values: [{ type: 'direct', value: '0.97' }] });
                                        (newWrap.data as FactoryUtil.NodeParamManager).params.push(paramWrap);
                                        newNode.children.push(TreeUtil.createNode(paramWrap, newNode));
                                    }
                                    {
                                        const paramWrap = FactoryUtil.createParamWrap('modulate-item',
                                            { name: 'decay_limit', formulaItems: ['var'], values: [{ type: 'direct', value: '5' }] });
                                        (newWrap.data as FactoryUtil.NodeParamManager).params.push(paramWrap);
                                        newNode.children.push(TreeUtil.createNode(paramWrap, newNode));
                                    }
                                    {
                                        const paramWrap = FactoryUtil.createParamWrap('modulate-item',
                                            { name: 'gravity', formulaItems: ['var'], values: [{ type: 'direct', value: '0.1' }] });
                                        (newWrap.data as FactoryUtil.NodeParamManager).params.push(paramWrap);
                                        newNode.children.push(TreeUtil.createNode(paramWrap, newNode));
                                    }

                                    setFocusNode({ ...node });
                                    invalidate();
                                }
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Inherit'}
                            width={300}
                            isUse={hasInheritNode}
                            callback={() => {
                                const data = (wrap.data as FactoryUtil.NodeExtendManager);
                                if (hasInheritNode) {
                                    console.log(data.mngs[data.mngs.length - 1].type);
                                    data.mngs.splice(data.mngs.length - 1, 1);
                                    node.children.splice(node.children.length - 1, 1);
                                    setFocusNode({ ...node });
                                    invalidate();
                                } else {
                                    const newWrap: FactoryUtil.WrapNode = {
                                        type: 'connect-mng',
                                        data: {
                                            connects: []
                                        } as FactoryUtil.NodeInheritConnectManager
                                    }
                                    data.mngs.push(newWrap);
                                    const newNode = TreeUtil.createNode(newWrap, node);
                                    node.children.push(newNode);
                                    setFocusNode({ ...node });
                                    invalidate();
                                }
                            }}
                        />
                    }
                />
            </>}
        />
    </>);
}

export default FactDetExtendMng;